.main h1 {
    text-align: center;
}

.main {
    display: flex;
    justify-content: center;
    align-items: top;
    width: 100%;
    height: 100%;
    margin-left: 260px;
    background-image: url('SubtleBckg.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    overflow-y: auto
}

.main-bestiary {
    display: flex;
    justify-content: center;
    align-items: top;
    width: 100%;
    height: 100%;
    margin-left: 260px;
    background-image: url('BestiaryBckg.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    overflow-y: auto
}

.content {
    font-size: 15px;
    background-color: #333333ea;
    max-width: 800px;
    padding-top: 22px;
    padding-bottom: 20px;
    text-align: left;
    color: white;
    padding-left: 80px;
    padding-right: 80px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow for depth */
}

.content a {
    color: aqua;
}

.content li {
    text-decoration: none;
}

.quote {
    background-color: rgb(45, 45, 45);
    border: 1px solid black;
    border-radius: 6px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 12px;
    padding-right: 12px;
    margin-left: -10px;
    gap: 12px;
}

.smallpic {
    border-radius: 18px;
    width: 200px;
    height: 70px;
}

.pic {
    border-radius: 18px;
}

.image-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
  
.scaled-down-pic-large {
    border-radius: 10px;
    width: auto;
    height: auto;
    max-width: 90%;
    max-height: 90%;
    display: block;
}

.scaled-down-pic-medium {
    border-radius: 10px;
    width: auto;
    height: auto;
    max-width: 70%;
    max-height: 70%;
    display: block;
}
  
.scaled-down-pic {
    border-radius: 10px;
    width: auto;
    height: auto;
    max-width: 30%;
    max-height: 30%;
    display: block;
}

.scaled-down-pic-small {
    border-radius: 10px;
    width: auto;
    height: auto;
    max-width: 20%;
    max-height: 20%;
    display: block;
    object-fit: contain;
}

.icon {
    vertical-align: middle;
    width: 16px;
    height: 16px;
}

.bestiary-header h2 {
    color: #3ce5ff;
}

.bestiary-content {
    display: flex;
    gap: 0px;
}

.ability {
    display: flex;
    padding-left: 80px;
    padding-right: 80px;
    margin-left: -80px;
    margin-right: -80px;
    background-color: rgba(87, 76, 76, 0.296);
}

.ability-secondcolumn{
    padding-top: 40px;
}
  