/* General App Styles */
.App {
  display: flex;
  height: 100%;
}

.App-logo {
  justify-content: flex-start;
  width: 180px;
  height: 80px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 10px;
}

@font-face {
  font-family: 'Audiowide';
  src: url('fonts/Audiowide-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.sidebar {
  background-color: #24282c;
  color: white;
  overflow-y: auto;
  padding-top: 20px;
  height: 100vh;
  position: fixed;
}

.sidebar .App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid #3ce5ff;
}

.link-tree {
  list-style: none;
  padding: 0px;
}

.link-tree li {
  padding-top: 5px;
  padding-bottom: 5px;
}

.tree-node {
  padding-left: 20px;
  cursor: pointer;
  color: #3ce5ff;
  display: block;
}

.tree-node:hover {
  color: aqua;
}

.tree-node-icon {
  font-size: 10px;
  margin-right: 10px;
}

/* Leaf Node Styling */
.tree-leaf {
  padding-left: 40px;
  color: white;
  text-decoration: none;
  display: block;  /* Ensures the entire area of the list item is clickable */
}

.link-tree li.selected {
  background-color: #4caf4f79;
}

.link-tree li.selected .tree-node,
.link-tree li.selected .tree-leaf {
  color: white;
}

.tree-leaf:hover,
.tree-node:hover {
  color: aqua;
}

.tree-leaf.selected {
  background-color: #4caf4f79; 
}

/* Container with the scrollbar */
.link-tree-container {
  max-height: calc(100vh - 110px); /* Adjust height to fit within available space */
  overflow-y: auto; /* Enables vertical scrolling */
}

/* The width of the scrollbar */
.link-tree-container::-webkit-scrollbar {
  width: 10px;
}

/* The track of the scrollbar (background area) */
.link-tree-container::-webkit-scrollbar-track {
  background: #1b1b1b;
}

/* The handle of the scrollbar (thumb) */
.link-tree-container::-webkit-scrollbar-thumb {
  background: linear-gradient(145deg, #218696, #114f9f); /* Gradient effect for the thumb */
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); /* Inner shadow for depth */
  border-radius: 40px;
  height: 260px;
}

/* Hover effect for the thumb */
.link-tree-container::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(145deg, #1a73e8, #3ce5ff); /* Reversed gradient on hover */
  box-shadow: inset 0 0 8px rgba(0, 204, 255, 0.5); /* Slight glow */
}

